  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Circular-Pro-Black';
  src: url('https://d17s4kc6349e5h.cloudfront.net/legoland/assets/fonts/lineto-circular-pro-black.woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Circular-Pro-Bold';
  src: url('https://d17s4kc6349e5h.cloudfront.net/legoland/assets/fonts/lineto-circular-pro-bold.woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'iconfont';
  src: url('https://d17s4kc6349e5h.cloudfront.net/legoland/assets/fonts/iconfont.eot?#') format('eot'), url('https://d17s4kc6349e5h.cloudfront.net/legoland/assets/fonts/iconfont.woff') format('woff'), url('https://d17s4kc6349e5h.cloudfront.net/legoland/assets/fonts/iconfont.ttf') format('truetype'), url('https://d17s4kc6349e5h.cloudfront.net/legoland/assets/fonts/iconfont.svg#iconfont') format('svg');
}
.header {
  background-color: #facd22;
  background-image: url('https://d17s4kc6349e5h.cloudfront.net/legoland/assets/images/background-pattern-transparent.png');
  padding: 30px;
  text-align: center;
  display: block;
  z-index: 1;
}
.flickr-logo {
  height: 100px;
  width: 200px;
}
.flickr-logo__circle-1,
.flickr-logo__circle-2 {
  cy: 50%;
  r: 28%;
}
.flickr-logo__circle-1 {
  fill: #006cb7;
  cx: 25%;
}
.flickr-logo__circle-2 {
  fill: #d01012;
  cx: 75%;
}



